import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
  export default 
  {
      data() {
      return {
          balance:0,
		  info_form:
		  {
			  
		  	  balance_money:0
		  }	
    }
      },
    methods:
    {
    	confirm(type){
    		//withdraw_money
			var that = this;
            if(parseFloat(this.info_form.balance_money)>parseFloat(this.balance))
            {
                MessageBox.alert('提现金额不可大于您的余额', '提示');
                return;

            }
		
            if(this.info_form.balance_money == undefined || this.info_form.balance_money<=0 || this.info_form.balance_money==0)
            {
                MessageBox.alert('提现金额不可低于0元', '提示');
                return;

            }

			$http.get('finance.balance.withdraw', { withdraw_type:type , withdraw_money: this.info_form.balance_money }, '正在提交数据...').then(function (response) {

				if (response.result == 1) {
                    MessageBox.alert(response.msg, '提示');
					that.$router.go(-1);
					

				}else
                {
                    MessageBox.alert(response.msg, '提示');
                }
			}, function (response) {
				// error callback
			});
    	}
    },
    activated () 
    {
        this.balance = this.$route.params.balance;
    },
    components: { cTitle }  
   }   


